// src/pages/ManutencaoInstalacoes.js
import React from 'react';
import { Form, Button, Table } from 'react-bootstrap';

function ManutencaoInstalacoes() {
  return (
    <div>
      <h2>Manuteno de Instalaes</h2>
      <Form>
        <Form.Group controlId="manutencaoDescricao">
          <Form.Label>Descrio da Manuteno</Form.Label>
          <Form.Control as="textarea" rows={3} placeholder="Descreva a manuteno necessria" />
        </Form.Group>
        <Form.Group controlId="dataManutencao">
          <Form.Label>Data Prevista</Form.Label>
          <Form.Control type="date" />
        </Form.Group>
        <Button variant="primary" type="submit" className="mt-3">Registrar Manuteno</Button>
      </Form>

      <h3 className="mt-4">Manutenes Registradas</h3>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Descrio</th>
            <th>Data</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>Reparo no canil</td>
            <td>2024-10-15</td>
          </tr>
          <tr>
            <td>Troca de lmpadas</td>
            <td>2024-10-20</td>
          </tr>
        </tbody>
      </Table>
    </div>
  );
}

export default ManutencaoInstalacoes;
